import React, { useEffect } from "react";
import "./App.css";
import AMapLoader from "@amap/amap-jsapi-loader";
import qs from "qs";

const BACKEND_URL =
  process.env.NODE_ENV === "production" ? "" : "http://localhost:30000";

const DEMO_DATA = [
  { lng: 116.191031, lat: 39.988585, count: 10 },
  { lng: 116.389275, lat: 39.925818, count: 11 },
  { lng: 116.287444, lat: 39.810742, count: 12 },
  { lng: 116.481707, lat: 39.940089, count: 13 },
  { lng: 116.410588, lat: 39.880172, count: 14 },
  { lng: 116.394816, lat: 39.91181, count: 15 },
  { lng: 116.416002, lat: 39.952917, count: 16 },
];

function App() {
  // React Hooks 
  // 
  // React
  const [value, setValue] = React.useState<string>();
  const [returnValue, setReturnValue] = React.useState<any>();

  // JS
  const [AMap, setAMap] = React.useState<any>();
  // 
  const [map, setMap] = React.useState<any>();
  // 
  // TODO: APIsetDataSet
  const [heatmap, setHeatmap] = React.useState<any>();

  useEffect(() => {
    // 
    AMapLoader.load({
      // TODO: JS APIKey
      key: "*****************************",
      version: "2.0",
      plugins: ["AMap.HeatMap"], // 'AMap.Scale'
    })
      .then((AMap) => {
        setAMap(AMap);
        const map = new AMap.Map("container", {
          //id
          viewMode: "2D", //3D
          zoom: 11, //
          center: [116.4, 39.9], //
        });
        map.plugin(["AMap.HeatMap"], function () {
          // TODO: 
          //heatmap
          const heatmap = new AMap.HeatMap(map, {
            radius: 25, //
            opacity: [0, 0.8],
            /*,
            gradient:{
                0.5: 'blue',
                0.65: 'rgb(117,211,248)',
                0.7: 'rgb(0, 255, 0)',
                0.9: '#ffea00',
                1.0: 'red'
            }
             */
          });
          // TODO: 
          heatmap.setDataSet({
            data: DEMO_DATA,
            max: 20,
          });
          setHeatmap(heatmap);
        });
        setMap(map);
      })
      .catch((e) => {
        console.log(e);
      });
  }, []);

  // => JS/TS
  const handleChange = (event: any) => {
    // 
    console.log("", event);
    setValue(event.target.value);
  };

  // TODO: 
  const handleSubmit = async (event: any) => {
    // 
    event.preventDefault();
    console.log("", event);
    const res = await fetch(`${BACKEND_URL}/api/price`, {
      method: "POST",
      // TODO: headers"Content-Type"body
      headers: {
        "Content-Type": "application/json",
      },
      body: qs.stringify(event),
    });
    // JSON
    const data = await res.json();
    console.log("", data);
    setReturnValue(data);
  };

  // JSX
  return (
    <div className="App">
      {/* TODO:  */}
      <form onSubmit={handleSubmit}>
        <label>
          
          <input type="text" value={value} onChange={handleChange} />
        </label>
        <input type="submit" value="" />
      </form>
      <br />
      {`${returnValue}`}
      {/*  */}
      <div
        id="container"
        className="map"
        style={{ height: "700px", width: "100%" }}
      ></div>
    </div>
  );
}

export default App;
